body {
  background: #555555;
  color: #d8d8d8;
  font-family: 'Montserrat', sans-serif;
  font-size: 1rem; }

main {
  background: #232323; }

h1, h2 {
  font-weight: 100;
  text-transform: uppercase; }

ul {
  margin: 0;
  padding: 0; }
  ul li {
    list-style: none; }

p {
  margin: 0;
  padding: 0; }

.center {
  text-align: center; }

.rose {
  color: #f65261; }

.wrapper {
  max-width: 75rem;
  margin: 0 auto;
  padding: 2rem 3rem; }

header {
  margin-bottom: .5rem;
  position: relative;
  overflow: hidden;
  padding: 3rem 6rem; }
  header:before, header:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 0; }
  header:before {
    background: url("../img/back-img.jpg");
    filter: blur(0.25rem); }
  header:after {
    background: rgba(0, 0, 0, 0.5); }
  header * {
    position: relative;
    z-index: 1; }

.main {
  padding: 1rem 3rem; }

footer {
  padding: 2rem 3rem;
  background-color: rgba(66, 66, 66, 0.85); }

.search-box {
  display: flex;
  justify-content: space-between; }
  .search-box input {
    flex-basis: 67%; }
  .search-box button {
    flex-basis: 25%; }

.add-movie-box {
  position: absolute;
  top: 1rem;
  right: 3rem; }

.movie-list-box {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.btn {
  padding: 1rem 2rem;
  border-radius: .5rem;
  background: #f65261;
  border: 1px solid #f65261;
  color: #fff;
  font-size: 1rem;
  text-transform: uppercase; }
  .btn--secondary {
    background: rgba(85, 85, 85, 0.85);
    border: 0;
    color: #f65261; }
  .btn--reset {
    background: colour-slate;
    border: 1px solid #f65261;
    color: #f65261; }

input[type=text] {
  padding: 1rem 2rem;
  background-color: rgba(66, 66, 66, 0.8);
  border: 1px solid rgba(66, 66, 66, 0.8);
  border-radius: .5rem; }

.card {
  flex-basis: 30%;
  position: relative;
  margin: 1rem 0 2rem; }
  .card img {
    max-width: 100%;
    margin-bottom: 1rem; }
  .card-info {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }
  .card-title {
    flex-basis: 70%;
    margin-bottom: .5rem; }
  .card-date {
    font-size: .75rem;
    line-height: 1rem;
    padding: .25rem .5rem;
    border: 1px solid;
    border-radius: .25rem; }
  .card-ganre {
    font-size: .75rem; }
  .card-icon {
    display: block;
    height: 2rem;
    width: 2rem;
    position: absolute;
    top: 1rem;
    right: 1rem;
    font-size: 2rem;
    line-height: .825rem;
    background: #232323;
    border-radius: 50%;
    transform: rotate(90deg);
    text-align: center; }
